import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import assign from 'object-assign';
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import * as tensorflowActions from "../actions/tensorflowActions";

class DrawableCanvas extends React.Component {

  constructor (props) {
    super(props)
    this._strokes = [];
    this._points = [];
  }

  componentDidMount(){
    // document.body.style.overflow = "hidden";
    const canvas = ReactDOM.findDOMNode(this);

    // canvas.style.width = '100%';
    // canvas.style.height = '100%';
    canvas.width = 256//canvas.offsetWidth;
    canvas.height = 256//window.innerHeight-100;

    const context = canvas.getContext('2d');

    this.setState({
      canvas,
      context
    });
  }

  componentWillReceiveProps(nextProps) {
    if(nextProps.clear){
      this.resetCanvas();
      this._strokes = [];
      this._points = [];
    }
  }
  static getDefaultStyle() {
    return {
      brushColor: '#FFFF00',
      lineWidth: 8,
      cursor: 'pointer',
      canvasStyle: {
        backgroundColor: '#00FFDC'
      },
      clear: false
    };
  }

  handleOnTouchStart (e) {
    const rect = this.state.canvas.getBoundingClientRect();
    this.state.context.beginPath();
    this._points = [[e.targetTouches[0].pageX - rect.left, e.targetTouches[0].pageY - rect.top]]
    this.setState({
      lastX: e.targetTouches[0].pageX - rect.left,
      lastY: e.targetTouches[0].pageY - rect.top,
      drawing: true
    });
  }

  handleOnMouseDown(e){
    const rect = this.state.canvas.getBoundingClientRect();
    this.state.context.beginPath();
    this._points = [[e.clientX - rect.left, e.clientY - rect.top]]
    this.setState({
      lastX: e.clientX - rect.left,
      lastY: e.clientY - rect.top,
      drawing: true
    });
  }

  handleOnTouchMove (e) {
    if (this.state.drawing) {
      const rect = this.state.canvas.getBoundingClientRect();
      const lastX = this.state.lastX;
      const lastY = this.state.lastY;
      let currentX = e.targetTouches[0].pageX - rect.left;
      let currentY = e.targetTouches[0].pageY - rect.top;
      this.draw(lastX, lastY, currentX, currentY);
      this.setState({
        lastX: currentX,
        lastY: currentY
      });
    }
  }

  handleOnMouseMove(e){
    if(this.state.drawing){
      const rect = this.state.canvas.getBoundingClientRect();
      const lastX = this.state.lastX;
      const lastY = this.state.lastY;
      let currentX = e.clientX - rect.left;
      let currentY = e.clientY - rect.top;

      this.draw(lastX, lastY, currentX, currentY);
      this.setState({
        lastX: currentX,
        lastY: currentY
      });
    }
  }

  handleonMouseUp() {
    this._strokes.push(this._points);
    this.setState({
      drawing: false
    });
    let imageData = this.state.context.getImageData(
      0,
      0,
      this.state.canvas.width,
      this.state.canvas.height
    );

    this.props.actions.predict(this._strokes, this.state.canvas.width, this.state.canvas.height, imageData);
  }

  draw(lX, lY, cX, cY) {
    this._points.push([cX, cY]);
    const newContext = this.state.context;
    newContext.strokeStyle = this.props.brushColor;
    newContext.lineWidth = this.props.lineWidth;
    this.setState({
      context: newContext
    });
    this.state.context.moveTo(lX, lY);
    this.state.context.lineTo(cX, cY);
    this.state.context.stroke();
  }

  resetCanvas(){
    const width = this.state.context.canvas.width;
    const height = this.state.context.canvas.height;
    this.state.context.clearRect(0, 0, width, height);
  }

  canvasStyle(){
    const defaults = DrawableCanvas.getDefaultStyle();
    const custom = this.props.canvasStyle;

    return assign({}, defaults, custom);
  }

  render() {
    return (
      <canvas style = {this.canvasStyle()}
        onMouseDown = {this.handleOnMouseDown.bind(this)}
        onTouchStart = {this.handleOnTouchStart.bind(this)}
        onMouseMove = {this.handleOnMouseMove.bind(this)}
        onTouchMove = {this.handleOnTouchMove.bind(this)}
        onMouseUp = {this.handleonMouseUp.bind(this)}
        onTouchEnd = {this.handleonMouseUp.bind(this)}
      >
      </canvas>
    );
  }

}

DrawableCanvas.propTypes = {
  brushColor: PropTypes.string,
  lineWidth: PropTypes.number,
  cursor: PropTypes.string,
  canvasStyle: PropTypes.shape({
    backgroundColor: PropTypes.string
  }),
  clear: PropTypes.bool,
  actions: PropTypes.object.isRequired
};

function mapStateToProps(state, ownProps) {
  return {};
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(tensorflowActions, dispatch)
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DrawableCanvas);
