import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import * as tensorflowActions from "../actions/tensorflowActions";
import DrawableCanvas from "./DrawableCanvas";
import PropTypes from 'prop-types';
import { TouchScrollable } from 'react-scrolllock';

class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      brushColor: "#000000",
      lineWidth: 8,
      canvasStyle: {
        backgroundColor: "#FFFFFF",
        borderColor: "#786B6B",
        borderStyle: "solid"
      },
      clear: false
    };
  }

  componentDidMount() {
    this.props.actions.init();
  }

  handleOnClickClear() {
    this.setState({clear: true},
      () => {
        this.setState({ clear: false });
      }
    );
    this.props.actions.loadPredictionSuccess("Model is loaded")
  }

  render() {
    return (
      <div>
        <label>Predicted class: {this.props.prediction}</label>{' '}
        <button className="ui button yellow" onClick={() => this.handleOnClickClear()}>Clear</button>
        <br></br>
        <TouchScrollable>
          <DrawableCanvas {...this.state}/>
        </TouchScrollable>
      </div>
    );
  }
}

Board.propTypes = {
  prediction: PropTypes.string.isRequired,
  actions: PropTypes.object.isRequired
};

function mapStateToProps(state, ownProps) {
  return {
    prediction: state.prediction
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(tensorflowActions, dispatch)
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Board);
